import Layout from "@/views/layout/App.vue";
export default {
    path: "/sys",
    component: Layout,
    name: "sys",
    redirect: "/sys/list",
    meta: {
        title: "系统设置"
    },
    children: [
        {
            path: "notice",
            name: "noticelist",
            meta: {
                title: "公告管理"
            },
            component: resolve =>
                require(["@/views/sys/notice.vue"], resolve)
        },
        {
            path: "config",
            name: "tasklist",
            meta: {
                title: "配置管理"
            },
            component: resolve =>
                require(["@/views/sys/config.vue"], resolve)
        },

    ]
};
