import Layout from "@/views/layout/App.vue";
export default {
    path: "/cash",
    component: Layout,
    name: "cash",
    redirect: "cashOutIn",
    meta: {
        title: "财务管理"
    },
    children: [
        {
            path: "cashFlow",
            name: "cashFlow",
            meta: {
                title: "用户钱包交易记录"
            },
            component: resolve =>
                require(["@/views/cash/cashFlow/cashFlow.vue"], resolve)
        },
        {
            path: "payment",
            name: "payment",
            meta: {
                title: "用户支付记录"
            },
            component: resolve =>
                require(["@/views/cash/payment/payment.vue"], resolve)
        }
    ]
};
