import Layout from "@/views/layout/App.vue";
export default {
  path: "/user",
  component: Layout,
  name: "user",
  redirect: "/user/list",
  meta: {
    title: "用户管理"
  },
  children: [
    {
      path: "list",
      name: "userlist",
      meta: {
        title: "用户列表"
      },
      component: resolve =>
        require(["@/views/user/user.vue"], resolve)
    },
    {
      path: "commentlist",
      name: "commentlist",
      meta: {
        title: "用户评论"
      },
      component: resolve =>
        require(["@/views/user/comment.vue"], resolve)
    }
  ]
};
