import Layout from "@/views/layout/App.vue";
export default {
  path: "/goods",
  component: Layout,
  name: "goods",
  redirect: "/goods/list",
  meta: {
    title: "商品管理"
  },
  children: [
    {
      path: "list",
      name: "goodslist",
      meta: {
        title: "商品列表"
      },
      component: resolve =>
        require(["@/views/goods/goods/goods.vue"], resolve)
    },
    {
      path: "category",
      name: "categorylist",
      meta: {
        title: "商品类目"
      },
      component: resolve =>
        require(["@/views/goods/category/category.vue"], resolve)
    },
    {
      path: "add",
      name: "goodsAdd",
      meta: {
        title: "编辑商品"
      },
      component: resolve =>
        require(["@/views/goods/goods/goodsAdd.vue"], resolve)
    }
  ]
};
