<template>
  <div class="pagination">
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pageParams.pageNum"
      :page-sizes="pageSizes"
      :page-size="pageParams.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
import Config from "@/config/app.js";
import { getDateStr } from "@/utils/date.js";
export default {
  name: "Pagination",
  props: {
    requestFunc: [Function, Boolean],
    filterParams: {
      type: Function,
      default: p => {
        return p;
      }
    },
    params: Object,
    paginationField: {
      type: String,
      default: ""
    }
  },
  data: () => {
    return {
      total: 0,
      pageSizes: Config.pageSizesArr,
      pageParams: {
        pageSize: Config.pageSizesArr[0],
        pageNum: 1
      }
    };
  },
  methods: {
    handleSizeChange(val) {
      this.pageParams.pageSize = val;
      this.getPageData();
    },
    handleCurrentChange(val) {
      this.pageParams.pageNum = val;
      this.getPageData();
    },
    getPageData() {
      if (!this.requestFunc) {
        this.$emit("returnData", []);
        return;
      }
      let cloneParam = {};
      for (let k in this.params) {
        let obj = this.params[k];
        if (k.toLowerCase() == "daterange") {
          cloneParam["startTime"] = "";
          cloneParam["endTime"] = "";
          if (!Array.isArray(obj)) continue;
          if (obj.length != 2) continue;
          cloneParam["startTime"] = getDateStr(obj[0]);
          cloneParam["endTime"] = getDateStr(obj[1]);
          continue;
        }
        if (Array.isArray(obj)) {
          cloneParam[k] = obj.join();
          continue;
        }
        cloneParam[k] = obj;
      }
      let p = this.filterParams({ ...cloneParam, ...this.pageParams });
      console.log(p);
      this.requestFunc(p).then(result => {
        let r = result.data;
        if (this.paginationField) {
          r = result[this.paginationField];
        }
        this.total = parseInt(r.total);
        this.$emit("returnData", r.records);
      });
    },
    Refresh() {
      this.pageParams.pageNum = 1;
      this.getPageData();
    },
    GetAllParam() {
      return { ...this.params, ...this.pageParams };
    }
  },
  mounted: function() {
    this.getPageData();
  }
};
</script>

<style scoped>
.pagination {
  padding: 10px;
  display: flex;
  background: #fff;
}
</style>
