import Layout from "@/views/layout/App.vue";
export default {
    path: "/order",
    component: Layout,
    name: "order",
    redirect: "/order/list",
    meta: {
        title: "订单管理"
    },
    children: [
        {
            path: "list",
            name: "orderlist",
            meta: {
                title: "订单列表"
            },
            component: resolve =>
                require(["@/views/order/order.vue"], resolve)
        },
        {
            path: "detail",
            name: "detail",
            meta: {
                title: "订单详情"
            },
            component: resolve =>
                require(["@/views/order/orderDetail.vue"], resolve)
        }
    ]
};
