import Layout from "@/views/layout/App.vue";
export default {
    path: "/express",
    component: Layout,
    name: "express",
    redirect: "/express/list",
    meta: {
        title: "物流管理"
    },
    children: [
        {
            path: "list",
            name: "express",
            meta: {
                title: "快递列表"
            },
            component: resolve =>
                require(["@/views/logistics/express.vue"], resolve)
        },
        {
            path: "expressCompany",
            name: "expressCompany",
            meta: {
                title: "快递公司"
            },
            component: resolve =>
                require(["@/views/logistics/expressCompany.vue"], resolve)
        }
    ]
};
